<template>
    <div class="NavBar" v-if="router.currentRoute.value.path != '/404'">
        <van-nav-bar :title="title" right-text="按钮" :left-arrow="leftArrow" @click-left="onClickLeft"
            @click-right="onClickRight">
            <template #right>
                <van-icon name="wap-home-o" size="18" v-show="rightArrow" />
            </template>
        </van-nav-bar>
    </div>
</template>

<script>
import { reactive, toRefs } from 'vue'
import { useRouter, useRoute } from 'vue-router'

export default {
    setup() {
        const data = reactive({
            leftArrow: true,
            rightArrow: true,
            title: undefined,
        })
        // 获取路由器实例
        const router = useRouter()
        //使用
        //router.push..... 等等方法

        // route响应式对象，监控变化，传值
        const route = useRoute
        //获取 处理route.query 等等

        const getPath = (values) => {
            // 拿到最新的路由

            //获取title
            data.title = router.currentRoute.value.meta.title
            //获取路由name
            let routerName = router.currentRoute.value.name;
            //判断是否在 home person 页面里面
            let _status = ['home', 'person'].indexOf(routerName) != -1 ? false : true;
            //赋值左/右图标显示/隐藏状态
            data.leftArrow = _status
            data.rightArrow = _status
        };
        //左边返回按钮
        const onClickLeft = () => {
            if(router.currentRoute.value.name == 'login'){
                router.push({ path: "/" })
            }else{
                history.back()
            }

        };
        //左边返回按钮
        const onClickRight = () => router.push({ path: "/" });
        return {
            ...toRefs(data),
            router,
            route,
            getPath,
            onClickLeft,
            onClickRight
        };
    },
    //侦听器 路由变化
    watch: {
        $route: "getPath",
    },
}
</script>

<style lang="less" scoped>
</style>