<template>
    <div class="login">
        <div class="login-main">
            <div class="login-top">
                <div class="title">hello!欢迎登录</div>
            </div>
            <div class="login-form">
                <van-form @submit="onSubmit">
                    <van-cell-group inset>
                        <van-field v-model="username" name="用户名" label="用户名" placeholder="用户名"
                            :rules="[{ required: true, message: '请填写用户名' }]" />
                        <van-field v-model="password" type="password" name="密码" label="密码" placeholder="密码"
                            :rules="[{ required: true, message: '请填写密码' }]" />
                    </van-cell-group>
                    <div style="margin: 16px;">
                        <van-button round block type="warning" native-type="submit">
                            点击登录
                        </van-button>
                    </div>
                </van-form>
            </div>
            <div class="login-bottom"> 
                <div class="reg">还没账号？<router-link to="/person">立即注册</router-link></div>
            </div>
        </div>

    </div>
</template>

<script>
// import { method } from 'lodash'
import { useRouter } from 'vue-router';
import { ref } from 'vue';
import {login} from '../utils'
import { Toast } from 'vant';

export default {
    setup() {
        const username = ref('');
        const password = ref('');
        const router = useRouter();
        const onSubmit = () => {
           let postData = {username:username.value,password:password.value}
           login(postData).then((res)=>{
                sessionStorage.setItem('userInfo',JSON.stringify({'username':res.username,'token':res.token}))
                Toast.success('登录成功');
                router.replace({path:'/'})
           })
           return;
        };

        return {
            username,
            password,
            onSubmit,
        };
    }

}
</script>

<style lang="less" scoped>
.login {
    position: absolute;
    background: #00ffff;
    width: 100%;
    height: calc(100% - 46px);
    background-image: linear-gradient(to right, #3399ff, rgba(51, 153, 255, .1));

    .login-main {
        .login-top {
            display: flex;
            justify-content: center;
         
            .title {
                font-size: 24px;
                margin: 20px 0;
            }
        }

        .login-form {
        }
        .login-bottom{
            display: flex;
            justify-content: center;
           .reg{
            font-size: 14px;
           }
        }
    }

}
</style>
