//二级路由
import index from "@/views/index";//公共部分加主体内容
import login from "@/views/login";//登录页面

//三级路由
import home from "@/views/home/home";//首页
import person from "@/views/person/index";//个人中心

var routes = [
    //404页面
    {
        path: '/404',
        component: () => import('../views/404')
    },
    {
        path: '/:pathMatch(.*)',
        redirect: '/404'
    },
    //登录页面
    {
        path: '/login',
        name: "login",
        component: login,
        meta: {
            title: '登录',
         }
    },
    {
        path: "/",
        name: "index",
        component: index,
        children: [
            {
                path: "/",
                name: "home",
                component: home,
                meta: {
                    title: '主页',
                 }
            },{
                path: '/person',
                name: "person",
                component: person,
                meta: {
                    title: '个人中心',
                 }
            },
        ]
    }
]

export default routes
